 
 .container{
     width: 95%;
     display: flex;
     justify-content: space-evenly;
     flex-wrap: wrap;
 }

 .card{
     width: 370px;
 }

 .first{
     border-bottom: 40px solid rgba(255,0,0,.5);
 }
 .second{
    border-bottom: 40px solid rgba(0,255,0, .5);
}
.third{
    border-bottom: 40px solid rgba(0,0,255,.5);
}

@media screen and (max-width: 820px) {
    .first, .second, .third {
      width: 92%;
      margin-left: 4%;
    }
  }